.page {
	width: 100%;
	height: 100%;

	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;

	position: absolute;
	z-index: 1;

	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
}
.logo {
	width: 468px;
	height: 286px;

	margin-bottom: 20px;
}
.loading {
	margin-top: 100px;
}

.loaderPosition {
	margin-top: 120px;
}